@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono:ital,wght@0,400;0,700;1,400&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  box-sizing: border-box;
  background-color: #102027;
  color: aliceblue;
}
*, *:before, *:after {
  box-sizing: inherit;
}
.App {
  text-align: center;
  font-family: 'Fira Mono', 'Ubuntu Mono', monospace;
  margin: 5px 10px;
}
* {
  text-align: left;
}

button {
  cursor: pointer;
}

#headerContainer {
  min-height: 75px;
  max-height: 100px;
  width: 100%;
  border-bottom: 5px solid aliceblue;
  padding: 0;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

#logoContainer {
  max-height: 70rem;
  padding: 0;
  margin: 0;
  display: block;
  max-width: 100px;
  height: auto;
  clear: both;
  position: relative;
}

#logoImage {
  pointer-events: none;
  margin: 5px;
  padding: 0;
  display: block;
  width: auto;
  height: 90%;
  position: relative;
}

#logoButton {
  background: transparent;
  border: none !important;
  font-size: 0;
  margin: auto;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0%;
  left: 0%;
}

#navbarContainer {
  text-align: center;
  max-height: 100%;
  list-style-type: none;
  display: inline-flex;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  flex-grow: 1;
}

.headerButtonLi {
  color: aliceblue;
  /* display: block; */
  padding: 0;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 2em;
  border-bottom: 3px solid aliceblue;
  display: inline-block;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  overflow: hidden;
}
.headerButtonLi:before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 100%;
  top: 0;
  background: aliceblue;
  height: 4px;
  transition-property: right;
  transition-duration: 0.2s;
  transition-timing-function: ease-out;
}
.headerButtonLi:hover:before,
.headerButtonLi:focus:before,
.headerButtonLi:active:before {
  right: 0;
}

.headerButtonLi button {
  text-decoration: none;
  display: block;
  padding: 1em;
  color: aliceblue;
  font-family: inherit;
  background: none;
  border: none;
}

